.red-background,
.activate-red .cds--side-nav__items {
    background-color: #ffeaea;
}

a.cds--side-nav__link[aria-current='page']::before,
a.cds--side-nav__link--current::before {
    background-color: red;
}

.cds--side-nav__link:focus,
.cds--side-nav__menu-item:focus,
.cds--side-nav__submenu:focus {
    outline: 2px solid red;
}

.activate-red
    .cds--side-nav__item:not(.cds--side-nav__item--active):hover
    .cds--side-nav__item:not(.cds--side-nav__item--active)
    > .cds--side-nav__submenu:hover,
.activate-red .cds--side-nav__item:not(.cds--side-nav__item--active) > .cds--side-nav__link:hover,
.activate-red .cds--side-nav__menu a.cds--side-nav__link:not(.cds--side-nav__link--current):not([aria-current='page']):hover,
.activate-red .cds--side-nav a.cds--header__menu-item:hover,
.activate-red .cds--side-nav .cds--header__menu-title[aria-expanded='true']:hover,
.activate-red .cds--side-nav__menu a.cds--side-nav__link--current,
.activate-red .cds--side-nav__menu a.cds--side-nav__link[aria-current='page'],
.activate-red a.cds--side-nav__link--current {
    background-color: #ffcaca;
}

.cds--side-nav__item--active .cds--side-nav__submenu[aria-expanded='false']::before {
    background-color: red;
}

.activate-red.cds--header {
    background-color: #ffeaea;
}

.activate-red .cds--header__menu-item {
    background-color: #ffeaea;
}

.activate-red .cds--header__menu-item:hover {
    background-color: #ffcaca;
}

.activate-red .cds--header__menu-item:focus,
.activate-red .cds--header__name:focus {
    border: 2px solid red;
}

.cds--side-nav__items {
    padding-top: 0;
}

.footer {
    margin: 20px;
    bottom: 0;
    font-size: smaller;
}
